@import '../../../utils//utils.scss';
.section-item {
  box-sizing: border-box;
  padding: 20px;
  box-shadow: 0px 0px 5px #E2E2E2;
  background: white;
  border-radius: 5px;
  border: 1px solid #DDDDDD;
  margin-bottom: 20px;
}
.bp3-dark .section-item {
  background-color: #394b5e;
  box-shadow: none;
  border: 1px solid #293742;
}
@media screen and (max-width: 800px) {
  .section-item {
    border-radius: 0px;
    padding: 10px;
  }
}