@import '../../utils/utils.scss';
.label-sec-item {
  color: $color2;
  background: $white4;
  padding: 3px 6px;
  margin: 4px 5px;
  display: inline-block;
  border-radius: 5px;
  transition: all .3s;
  font-size: .5em;
  &:hover {
    background: $color3;
    color: #FFFFFF;
    text-decoration: none;
  }
}
.label-sec-title {
  margin-bottom: 12px;
  font-size: .75em;
  color: $black2;
}
.label-sec-input {
  margin-bottom: 15px;
}
.bp3-dark {
  .label-sec-item {
    background: $bg2;
    color: $gray4;
    &hover {
      color: $color2;
      background: $white4;
    }
  }
}