@import '../../utils/utils.scss';
#header-container {
  position: fixed;
  width: 100vw;
  z-index: 100;
  .phone-pull-down {
    display: none;
  }
  .qr-button {
    position: relative;
    margin-left: 10px;
    &:hover .url-qr {
      display: inline;
    }
    .url-qr {
      display: none;
      transition: opacity .3s;
      position: absolute;
      right: 0;
      top: 50px;
      background: $white1;
      padding: 12px;
      border-radius: 5px;
      border: 1px solid $white4;
      box-shadow: 0px 0px 20px $white5;
    }
  }
}
#nav-content {
  max-width: 1440px;
  margin: 0 auto;
  a {
    color: inherit !important;
    &:hover {
      color: inherit;
      text-decoration-color: inherit;
    }
  }
  .nav_title a {
    font-family: air !important;
    text-decoration: none;
  }
}
#content-root.bp3-dark {
  .url-qr {
    background: $bg3 !important;
    border-color: $bg2 !important;
    box-shadow: 0px 0px 20px $bg1 !important;
  }
}

@media screen and (max-width: 800px) {
  .phone-pull-down {
    display: block !important;
    position: fixed;
    @include size(50px);
    top: 0;
    right: 0;
    box-sizing: border-box;
    padding: 12px;
    z-index: 102;
    transform: all .3s;
    transform: rotate(-270deg);
    &.show-list {
      transform: rotate(0);
    }
  }
  .bp3-navbar {
    height: auto !important;
    overflow: hidden;
    .bp3-align-right {
      float: none !important;
      height: auto !important;
      padding: 0 0 5px;
      overflow: hidden;
      display: none !important;
      &.show-list {
        display: block !important;
      }
      &>* {
        display: block !important;
        text-align: center;
        padding: 5px 0;
        box-sizing: border-box;
        margin: 0px 20vw;
        border-bottom: 1px solid $bg2;
        &:last-child {
          border-bottom: none;
        }
      }
      .bp3-button {
        width: 100%;
        background-color: transparent !important;
        background-image: none !important;
        box-shadow: none !important;
      }
    }
    .bp3-align-left {
      float: none !important;
      height: 50px !important;
      padding: 10px 0 0;
      overflow: hidden;
      display: block !important;
      &.show-list {
        height: auto !important;
      }
      &>* {
        display: block !important;
        text-align: center;
        padding: 5px 0;
        box-sizing: border-box;
        margin: 0px 20vw;
        border-bottom: 1px solid $bg2;
      }
      .bp3-navbar-divider {
        display: none !important;
      }
      a {
        float: none;
        button {
          width: 100%;
        }
      }
      .bp3-navbar-heading {
        width: 100%;
        display: block;
        padding-bottom: 16px;
        margin: 0;
      }
    }
  }
}